input[type="radio"], input[type="checkbox"] {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 0.0625rem;
  width: 0.0625rem;
  margin: -0.0625rem;
  padding: 0;
  border: 0; }

input[type="checkbox"] + label span {
  display: inline-block;
  margin-right: 10px;
  margin-top: -1px;
  width: 21px;
  height: 21px;
  vertical-align: top;
  background: url("../../img/checkbox_radio/Check_sprite.png") -21px top no-repeat;
  border-radius: 2px;
  cursor: pointer;
  box-sizing: border-box; }

input[type="checkbox"]:checked + label span {
  background: url("../../img/checkbox_radio/Check_sprite.png") 0px top no-repeat;
  transition: .5; }

input[type="checkbox"]:focus + label span {
  box-shadow: 0 0 10px #6c9c5a; }

input[type="radio"]:focus + label .answer {
  border: 1px dotted #6c9c5a; }

input[type="radio"] + label .custom {
  display: inline-block;
  margin-right: 10px;
  margin-top: 1px;
  width: 20px;
  height: 20px;
  vertical-align: top;
  background: url("../../img/checkbox_radio/radio.png") -1px top no-repeat;
  cursor: pointer; }

input[type="radio"]:checked + label .custom {
  background: url("../../img/checkbox_radio/radio.png") -21px top no-repeat;
  transition: .5; }

input[type="radio"]:checked + label .custom + .answer {
